.folder{
    display:flex;
    padding:1rem;
    color: black;
    background-color: white;
    border: 1px solid #06238a;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    margin: 1rem;
    border-radius: 6px;
    text-align: center;
    width: 300px;
}

.file{
    display:flex;
    padding:1rem;
    color: black;
    background-color: white;
    border: 1px solid #8a2b06;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    margin: 1rem;
    border-radius: 6px;
    text-align: center;
    width: 300px;
}